@import url(https://fonts.googleapis.com/css?family=Poppins);
/* General */
* {
  margin: 0;
  padding: 0;
}


body {
  background: #ebebeb;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Poppins';
}

.App {
  height: 400px;
  width: 300px;
  background: #ebebeb;
  position: relative;
}

/* Social*/ 
a {
  text-decoration: none;
  color: #303030;
}

.social {
  text-align: center;
  font-size: 50px;
  margin-top: 50px;
}

.fa:hover {
  opacity: 0.7;
}
.button {
  background: #ffffff;
  display: flex;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.05);
  align-items: center;
  justify-content: center;
  width: 25%;
  font-size: 24px;
  height: 20%;
  min-width: 25%;
}

.button[data-size='2'] {
  height: 40%;
}

.button[data-value='null'] {
  pointer-events: none;
}

.button:hover {
  cursor: pointer;
}

